h1, h2, h3 {
  margin: 0;
}

.page {
  color: #292929;
  scroll-behavior: smooth;
  font-family: Inter, sans-serif;
  font-weight: 700;
}

.page__body {
  box-sizing: border-box;
  margin: 0;
}

.page__body--with-menu {
  overflow: hidden;
}

.page__header {
  margin-bottom: 120px;
}

.page__menu {
  transition: transform .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(100%);
}

.page__menu:target {
  transform: translateX(0);
}

.page__section {
  margin-bottom: 120px;
}

.page__section-title {
  text-align: center;
  letter-spacing: -1.5px;
  margin-bottom: 48px;
  font-size: 32px;
  line-height: 100%;
}

@media (min-width: 724px) {
  .page__section-title {
    margin-bottom: 64px;
    font-size: 40px;
  }
}

@media (min-width: 1200px) {
  .page__section-title {
    font-size: 48px;
  }
}

.button {
  color: #fff;
  cursor: pointer;
  background: #292929;
  border: 3px solid #292929;
  border-radius: 40px;
  width: 100%;
  height: 56px;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  transition: background-color .3s;
}

.button:hover {
  color: #292929;
  background-color: #fff;
}

.button:active {
  color: #fff;
  background: #111;
  border: 3px solid #111;
}

.logo__image {
  width: 130px;
}

@media (min-width: 724px) {
  .logo__image {
    width: 190px;
  }
}

.icon {
  background-position: center;
  background-repeat: no-repeat;
  width: 18px;
  height: 18px;
  display: inline-block;
}

@media (min-width: 724px) {
  .icon {
    width: 24px;
    height: 24px;
  }
}

.icon__phone {
  background-image: url("phone.f8d8aad7.svg");
  background-size: cover;
  margin-right: 30px;
  text-decoration: none;
  position: relative;
}

@media (min-width: 724px) {
  .icon__phone {
    margin-right: 40px;
  }

  .icon__phone:before {
    content: "+1 234 5555-55-55";
    color: #292929;
    letter-spacing: 2px;
    text-transform: uppercase;
    opacity: 0;
    pointer-events: none;
    width: max-content;
    padding-right: 12px;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    transition: opacity .3s;
    display: inline-block;
    position: absolute;
    top: 50%;
    transform: translate(-100%, -50%);
  }

  .icon__phone:hover:before {
    pointer-events: all;
    opacity: 1;
  }
}

.icon__menu-opener {
  background-image: url("menu.620474b7.svg");
  transition: background-image .3s;
}

.icon__menu-opener:hover {
  background-image: url("menu_hover.ca64cb70.svg");
}

@media (min-width: 724px) {
  .icon__menu-opener {
    background-image: url("menu_4.4e43920f.svg");
    transition: background-image .3s;
  }

  .icon__menu-opener:hover {
    background-image: url("menu_4_hover.20694b5a.svg");
  }
}

.icon__cross {
  background-image: url("cross.5b020ae9.svg");
  background-size: cover;
  width: 16px;
  height: 16px;
}

@media (min-width: 724px) {
  .icon__cross {
    width: 24px;
    height: 24px;
  }
}

.top-nav {
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-align: center;
  align-items: center;
  display: -ms-flexbox;
  display: flex;
}

.top-nav--menu {
  margin-bottom: 54px;
}

.header {
  background-image: url("girl.7aa21909.png");
  background-position: center;
  background-size: cover;
}

.header__content {
  box-sizing: border-box;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: justify;
  justify-content: space-between;
  height: 100vh;
  padding: 40px 0 90px;
  display: -ms-flexbox;
  display: flex;
}

@media (min-width: 724px) {
  .header__content {
    padding: 52px 0 40px;
  }
}

@media (min-width: 1200px) {
  .header__content {
    padding-bottom: 64px;
  }
}

.header__title {
  letter-spacing: -1.5px;
  margin: 0;
  font-family: inherit;
  font-size: 32px;
  font-style: italic;
  font-weight: 800;
  line-height: 100%;
  position: relative;
}

.header__title:before {
  content: "";
  background-image: url("header.2d80a695.svg");
  background-size: cover;
  width: 32px;
  height: 27px;
  margin-bottom: 16px;
  position: absolute;
  bottom: 100%;
  left: 3px;
}

@media (min-width: 724px) {
  .header__title {
    font-size: 40px;
  }

  .header__title:before {
    background-image: url("soundWaves.589d4f0f.svg");
    width: 63px;
    height: 76px;
    margin-bottom: 40px;
    left: 5px;
  }
}

@media (min-width: 1200px) {
  .header__title {
    font-size: 48px;
  }

  .header__title:before {
    margin-bottom: 32px;
  }
}

.container {
  max-width: 1020px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (min-width: 724px) {
  .container {
    padding: 0 72px;
  }
}

@media (min-width: 1200px) {
  .container {
    padding: 0 120px;
  }
}

.menu {
  box-sizing: border-box;
  background-color: #e8eff9;
  height: 100vh;
  padding: 40px 0;
}

@media (min-width: 724px) {
  .menu {
    padding: 52px 0 40px;
  }
}

@media (min-width: 1200px) {
  .menu {
    padding-bottom: 64px;
  }
}

.menu__list {
  letter-spacing: 2px;
  text-transform: uppercase;
  margin-bottom: 40px;
  padding: 0;
  font-size: 22px;
  line-height: 22px;
  list-style: none;
}

.menu__item {
  margin-bottom: 32px;
}

.menu__item:last-child {
  margin-bottom: 0;
}

.menu__link {
  color: #292929;
  border-bottom: 1px solid rgba(0, 0, 0, 0);
  width: max-content;
  padding-bottom: 8px;
  text-decoration: none;
  transition: border-color .3s;
  position: relative;
}

.menu__link:hover {
  border-color: #292929;
}

.menu__call {
  color: #292929;
  text-decoration: none;
  display: block;
}

.menu__phone {
  text-transform: uppercase;
  margin-bottom: 16px;
  font-size: 16px;
  line-height: 27px;
}

.menu__order {
  letter-spacing: 2px;
  text-transform: uppercase;
  border-bottom: 1px solid #292929;
  width: max-content;
  padding-bottom: 8px;
  font-size: 12px;
  line-height: 16px;
  position: relative;
}

